import React from 'react';
import { Link } from 'react-router-dom'; // Para links e botes
import ContentCard from '../components/ContentCard/ContentCard'; // Importa o card reutilizvel
import './Home.css'; // Importaremos o CSS para estilizao

const featuredItems = [
  {
    id: 'sermon-001',
    title: 'A Graa Suficiente em Meio  Fraqueza',
    type: 'Sermo',
    date: '2024-03-17',
    reference: '2 Corntios 12:9-10',
    description: 'Uma reflexo sobre como o poder de Deus se aperfeioa em nossa fraqueza...', // Descrio mais curta para home
    detailsUrl: '/sermoes/graca-suficiente',
    pdfUrl: '/path/to/sermon-001.pdf'
  },
  {
    id: "redes-sociais-familia-alianca",
    title: "Redes Sociais: Perigos e Oportunidades para a Famlia.",
    type: "Estudo Bblico",
    reference: "Ef 4:29, 1 Corntios 10:31, Romanos 12:2",
    bookLink: "Famlia da Aliana",
    theme: "Vida Crist",
    format: "Congresso",
    description: "Uma reflexo sobre o uso das redes sociais  luz da f crist.",
    detailsUrl: "/estudos/redes-sociais-familia-alianca",
    pdfUrl: "/estudos/redes-sociais-familia-alianca.pdf"},
  {
    id: "as-institutas-i",
    title: "Institutas da Religio Crist - Volume 1",
    type: "Resumo de Livro",
    author: "Joo Calvino",
    description: "Um resumo estruturado da obra monumental...", // Descrio mais curta
    detailsUrl: "livros/institutas-calvino-resumo",
    pdfUrl: "/path/to/book-summary-001.pdf"
  },
];
// --- Fim dos Dados de Exemplo ---


const Home = () => {
  // Caminho da imagem (o mesmo de About.js ou outra foto)
  const profileImageUrl = '/images/pastor-foto.jpeg'; // Verifique se est em public/images/

  return (
    <div className="home-container">

      {/* --- Seo Hero (Apresentao) --- */}
      <section className="hero-section">
        <div className="hero-content">
          <div className="hero-text">
            <h1>Bem-vindo!</h1>
            <p className="hero-subtitle">
              Sou <b>Giovanni Moreira Guimares</b>, pastor presbiteriano e entusiasta do ensino bblico.
              Minha Jornada de F  um testemunho da graa de Deus, de um encontro transformador com Cristo 
              que me conduziu  Igreja. 
              Marcada por um profundo aprendizado e desafios, essa caminhada culminou no chamado ao 
              ministrio pastoral. Hoje, com 25 anos de experincia pastoral e slida formao teolgica, 
              minha paixo  o ensino expositivo da Palavra, fundamentado na f reformada e com um corao 
              missionrio, buscando edificar a Igreja e proclamar o Evangelho.
            </p>
            <p>
              Este espao  dedicado ao compartilhamento de sermes, estudos,
              anlises de livros e outros recursos para edificar sua f e
              aprofundar seu conhecimento da Palavra de Deus. Aproveite ao mximo!
            </p>
            <Link to="/sobre" className="hero-button">Saiba Mais Sobre Mim</Link>
          </div>
          <div className="hero-image-container">
             <img
                src={profileImageUrl}
                alt="Foto do Pastor [Seu Nome]"
                className="hero-photo"
             />
          </div>
        </div>
      </section>

      {/* --- Seo de Destaques --- */}
      <section className="featured-section">
        <h2>Contedo em Destaque</h2>
        <div className="featured-list">
          {featuredItems.map((item) => (
             // Renderiza os cards de destaque
             <ContentCard
                key={item.id}
                title={item.title}
                type={item.type}
                date={item.date}
                // Adaptar a referncia se for livro (mostrar autor talvez?)
                reference={item.type === 'Resumo de Livro' ? `Por ${item.author}` : item.reference}
                description={item.description}
                detailsUrl={item.detailsUrl}
                pdfUrl={item.pdfUrl}
              />
          ))}
        </div>
      </section>

      {/* --- Seo de Chamada para Ao (CTA) --- */}
      <section className="cta-section">
        <h2>Explore Mais</h2>
        <p>Navegue pelas diferentes sees para encontrar o contedo que mais lhe interessa.</p>
        <div className="cta-buttons">
            <Link to="/sermoes" className="cta-button">Ver Sermes</Link>
            <Link to="/estudos" className="cta-button">Ver Estudos</Link>
            <Link to="/livros" className="cta-button">Ver Livros</Link>
        </div>
      </section>

        {/* --- Opcional: Widget de Agenda (Simples) --- */}
      {/*
      <section className="agenda-widget-section">
            <h2>Prximos Eventos</h2>
            <p>Acompanhe os prximos compromissos pblicos.</p>
            {/* Aqui voc pode listar 1 ou 2 eventos manualmente ou,
                no futuro, buscar da API do Google Calendar */}
            {/*
            <ul>
                <li><strong>25/03/2024:</strong> Pregao na Igreja X - Tema Y</li>
                <li><strong>01/04/2024:</strong> Palestra sobre Z - Local W</li>
            </ul>
            <Link to="/agenda" className="widget-link">Ver Agenda Completa </Link>
        </section>
       */}

    </div>
  );
};

export default Home;
