import React from 'react';
import './About.css'; // Importaremos o CSS para estilizao

const About = () => {
  // Ajuste o caminho da imagem conforme onde voc a salvou
  // Se estiver em public/images/, o caminho  relativo  raiz do site
  const profileImageUrl = '/images/pastor-foto.jpeg'; // Exemplo

  return (
    <div className="about-container">
      <h1>Sobre Mim</h1>

      <section className="about-intro">
        {/* Imagem do Pastor */}
        <img
          src={profileImageUrl}
          alt="Foto do Pastor Giovanni" // Coloque seu nome aqui
          className="about-photo"
        />
        {/* Pargrafo Introdutrio */}
        <div className="intro-text">
          <p>
            Eu sou <b>Giovanni Moreira Guimares</b>, pastor da Igreja Presbiteriana do Brasil,
            e  uma alegria receb-lo neste espao onde compartilho meus sermes, estudos 
            bblicos e resumos de livros. Minha paixo  contemplar a beleza e a profundidade 
            das Escrituras, ajudando pessoas a crescerem em seu relacionamento com Cristo. 
            Abaixo, um rpido testemunho da <b>Minha Jornada de F</b> para motiv-lo a
            prosseguir na sua prpria caminhada. Se desejar, entre em contato para conversarmos 
            mais sobre a Palavra de Deus. 
            </p>
          {/* Pode adicionar mais um pargrafo introdutrio se desejar */}
        </div>
      </section>

      <section className="about-section">
        <h2>Minha Jornada de F</h2>
        <p>Apesar de no vir de uma famlia com forte tradio religiosa, desde muito cedo tive um interesse pela vida de f. Ainda na infncia recebi o convite de um amigo (Jos Claudinei dos Santos) para ser coroinha na Igreja de So Sebastio (Caapava-SP), onde comecei a dar meus primeiros passos na caminhada de f. A preparao para a cerimnia de confirmao (Crisma) e o envolvimento em outras atividades como retiros espirituais e encontros com outros pr-adolescentes e adolescentes despertou em mim certa atrao pelas coisas de Deus. Nessa poca, com minha iniciao na leitura da Palavra de Deus, algumas dvidas tambm comearam a surgir, especialmente sobre temas como Idolatria, confisso auricular, perdo de pecados e penitncia.</p> 
        <p>Foi no incio de 1985, quando iniciei o curso tcnico em Mecnica (ETESG Machado de Assis) que conheci o Henrique. Henrique era um homem casado, por volta de 35 anos, que na poca trabalhava na Engesa, uma pessoa formidvel. Rapidamente nos tornamos colegas de escola. Ele frequentava a Igreja Presbiteriana do Brasil em Caapava, e frequentemente fazia convites para que eu visitasse sua igreja. Confesso que na poca achava que os crentes eram todos um bando de subversivos que queria acabar com a Igreja Catlica. No gostava deles. Mas a amizade com Henrique, dia aps dia, ia me revelando uma pessoa diferente. Seus constantes e insistentes convites foram, como diz o ditado diz, gua mole em pedra dura.</p>
        <p>Certa vez, ele me convidou para ir ao aniversrio da Igreja. Na ocasio, viria um pastor de fora (Rev. Miguel ngelo) e seria um final de semana festivo. Pensei comigo, vou aproveitar a ocasio, visitar a igreja dele e pagar essa dvida, depois poderei recusar convites futuros com mais propriedade.</p>
        <p>Qual no foi minha surpresa...</p>
        <p>Na data e hora marcada cheguei  Igreja, sozinho, sem conhecer nada nem ningum, apenas o Henrique, que estava  minha espera, prximo  entrada da Igreja, junto de sua famlia. Ele me recebeu, me apresentou a algumas pessoas e fomos logo sentar num dos bancos, no meio da igreja. </p>
        <p>O ambiente do culto era diferente, no sei explicar o que me aconteceu. As msicas (lembro-me de: Solta o cabo da nau, toma os remos nas mos e navega com f em Jesus...), a pregao do pastor, a comunho dos irmos... aquilo tudo preencheu meu corao e serviu de combustvel ao Esprito Santo que o fez arder como fogo. Quando o pastor fez o apelo, sem que ningum me constrangesse, eu me levantei do banco e fui  frente. Glria a Deus. Bendito dia aquele que o Senhor me chamou.</p>
        <p>Desde ento, passei a me considerar um crente. Encontrei respostas para as minhas primeiras questes teolgicas e fui me inserindo naquela comunidade totalmente nova para mim, conhecendo outras pessoas maravilhosas, dentre elas no posso me esquecer de Eufrsio Rodrigues de Oliveira Filho, na poca presbtero da Igreja, hoje pastor, que tanto me ajudou nos meus anos iniciais.</p>
        <p>Mas a vida tem caminhos e descaminhos. Depois de algum tempo frequentando a igreja comecei a me afastar gradativamente, inicialmente devido a uma srie de compromissos: trabalho, escola, vida social com amigos e paqueras. O fato de ser o nico evanglico em casa at ento tambm colaborou para isso.</p> 
        <p>Esse afastamento da igreja me afastou de Cristo e de sua palavra, fazendo-me experimentar alguns dos pecados do mundo, mas o Senhor teve misericrdia de mim e por sua graa trouxe-me de volta  comunho dos santos na Igreja Presbiteriana de Mossor.</p>
        <p>Cheguei em Mossor em 1992. O Brasil vivia uma crise terrvel. Desemprego, altos ndices de inflao, incertezas polticas. Desempregado e vivendo uma tremenda crise existencial, fui para Mossor a passeio por sugesto de minha me, para visitar e conhecer a famlia. Vi nessa sugesto a direo de Deus para meu retorno ao convvio com os irmos.</p>
        <p>J no caminho, arrependido, vinha prometendo a Deus voltar ao rebanho de Cristo, andar nos seus santos caminhos. Ao chegar, fui bem recebido pelos familiares e, atravs do Nilton, esposo da minha tia Graa, consegui de imediato um emprego. Procurei logo a Igreja na cidade e encontrei a Igreja Presbiteriana Central de Mossor, que fica prximo  casa da minha Tia.</p>
        <p>Na Igreja Presbiteriana Central de Mossor recomecei minha caminhada de f, com a ajuda de muitos irmos, entre eles: Moura Jr., Rivanildo Medeiros, Airton Andrade, Aurino Dantas, Expedito Holanda, Odilon Teodsio e tantos outros cujos nomes Deus sabe e os recompensar na glria.</p>
        <p>Alguns momentos foram marcantes na minha trajetria, certamente o mais importante deles foi conhecer Abilene, que depois viria a se tornar minha esposa. Ela foi fundamental para o meu crescimento espiritual. Filha de pastor e com forte aspirao  obra missionria, ela chegou a se formar no Instituto Bblico do Norte. Abilene acreditou em mim antes de qualquer outra pessoa.</p>
        <p>Com Abilene compartilhei minhas mazelas, meus sonhos, alegrias e frustraes. Confesso que, em alguns momentos, cheguei a pensar que ela no suportaria. Hoje, j compartilhamos vinte e oito anos de casamento e temos um casal de filhos, a Giulia e o Arthur.</p>
        <p>Ao longo dos anos, sempre frequentando assiduamente aos trabalhos da Igreja e procurando aprender com os irmos, Deus foi colocando em meu corao o desejo de servir em sua causa. Participei de cursos para professores de Escola Dominical, e de evangelismo como Evangelismo Explosivo. Fui professor e superintendente de Escola Dominical e comecei a ensaiar minhas primeiras pregaes nas casas dos irmos e nos pontos de pregao.</p>
        <p>Vale ressaltar o apoio e a participao do Rev. Anselmo e Aldenisa, sua esposa, na minha vida. O pastor Anselmo sempre foi um referencial de vida e ministrio para mim. Moura Jr., foi fundamental nesse processo. Quantas e quantas vezes no fui  sua casa para tirar dvidas e ser aconselhado por ele. Serei eternamente grato por sua influncia em minha vida.</p>
      </section>

      <section className="about-section">
        <h2>Chamado para o Ministrio</h2>
        <p>Acredito que meu chamado efetivo para o ministrio tenha ocorrido numa programao de aniversrio da ento congregao do Planalto, hoje Igreja Presbiteriana do Planalto, em Mossor. Lembro-me que na ocasio o pregador, depois de expor o texto de Isaas 6, fez um apelo e no meu ntimo, impactado pela mensagem, respondi tambm: Envia-me a mim. Depois desse episdio passei a considerar o ministrio e compartilhei isso com meu pastor e com Abilene. Entretanto, sentia que precisava conhecer mais a Palavra de Deus e a doutrina da Igreja para servir melhor a Deus e sua Igreja. Foram trs longos anos como aspirante ao sagrado ministrio at poder ser aprovado pelo Conselho da Igreja e pelo Presbitrio para ser enviado ao Seminrio Presbiteriano do Norte, em Recife-PE, em fevereiro de 1997.</p>
        <p>Ordenado ao sagrado ministrio em dezembro de 2000, minha jornada pastoral comeou na Primeira Igreja Presbiteriana de Santarm (PA), onde servi por dois anos, seguida por seis anos na Igreja Presbiteriana de Caic (RN). Em 2009, retornei a Mossor, servindo inicialmente como pastor auxiliar na Igreja Central e, depois, por seis anos na Igreja Presbiteriana do Carnaubal. Subsequentemente, ministrei atos pastorais nas igrejas de Ipanguau e Novo Horizonte (Ass), e tambm nas de Barrocas e Redeno. Servi ainda por seis anos como pastor na Igreja Presbiteriana da Redeno e, nos ltimos anos, tenho atuado como pastor auxiliar na Igreja Presbiteriana de Barrocas. Paralelamente, no mbito do presbitrio, tive o privilgio de servir como presidente em duas legislaturas, como vice-presidente em quatro, como secretrio executivo em outras seis legislaturas e como primeiro e segundo secretrio por duas legislaturas.</p>
      </section>

      <section className="about-section">
        <h2>Formao Acadmica e Teolgica</h2>
        <p>Formado em Teologia pelo Seminrio Presbiteriano do Norte, em 2000, conclu uma especializao em Administrao Eclesistica em 2009 e em 2014 realizei o curso para a revalidao do Diploma de Bacharel em Teologia na Universidade Presbiteriana Mackenzie. Por fim, em 2021 cursei o Centro de Treinamento Missiolgico (CTM-SPN), sendo habilitado como plantador de igrejas nos projetos da IPB. Vale mencionar tambm minha graduao em Cincia da Computao pela Universidade Federal Rural do Semi rido (Ufersa) em 2023, uma formao que hoje considero valiosa por me auxiliar a usar ferramentas digitais para o avano do Reino.</p>
        {/* Pode usar uma lista <ul> se preferir:
        <ul>
          <li>Bacharel em Teologia - Seminrio Presbiteriano X (Ano Y)</li>
          <li>Bacharel em Cincia da Computao - Universidade Z (Ano W)</li>
          <li>[Outras formaes, cursos, ps-graduaes relevantes]</li>
        </ul>
        */}
      </section>

      <section className="about-section">
        <h2>Paixo pelo Ensino da Palavra</h2>
        <p>Toda essa jornada, desde as primeiras dvidas na infncia at os anos de ministrio pastoral e formao teolgica, convergiu para uma profunda paixo pelo ensino da Palavra de Deus. Essa paixo no  apenas um interesse intelectual, mas o motor que impulsiona meu servio a Cristo e  Sua Igreja, e se fundamenta em alguns pilares essenciais:</p>
          <li><b>Fidelidade ao sistema de doutrina presbiteriano e reformado:</b> Minha caminhada comeou com questionamentos que encontraram respostas slidas na teologia reformada. Encontrei no sistema doutrinrio presbiteriano uma estrutura robusta, bblica e coerente para compreender a soberania de Deus, a centralidade de Cristo, a obra do Esprito Santo e a autoridade das Escrituras. Essa fidelidade no  um tradicionalismo cego, mas uma convico firmada no estudo e na experincia de que esta tradio expressa de forma fiel o ensino bblico.  neste rico solo teolgico que busco nutrir minha prpria f e a f daqueles a quem sirvo.</li>
          <li><b>Compromisso com a Confisso de F de Westminster:</b> Como expresso madura e detalhada da f reformada, meu compromisso com a Confisso de F de Westminster e seus Catecismos Maior e Breve  fundamental. Vejo esses documentos como ferramentas valiosssimas, no para substituir a Bblia, mas para auxiliar na sua compreenso e ensino sistemtico. Eles so um guia seguro que resume as doutrinas essenciais da f crist, protege a Igreja de erros teolgicos e oferece uma base slida para a instruo, o discipulado e a vida piedosa. Meu ministrio busca refletir essa herana confessional, prezando pela s doutrina.</li>
          <li><b>nfase na pregao expositiva e ensino confessional:</b> Acredito firmemente que a maneira mais eficaz e fiel de alimentar o rebanho de Cristo  atravs da pregao expositiva, que busca desvendar o significado do texto bblico em seu contexto original e aplic-lo  vida contempornea. Permitir que a Palavra de Deus fale por si mesma, expondo seu sentido de forma clara e ordenada,  meu objetivo a cada sermo. Da mesma forma, no ensino em Escola Dominical, grupos de estudo ou discipulado, busco um ensino confessional, alinhado com nossas bases doutrinrias, para que os crentes cresam no conhecimento de Deus e sejam edificados sobre um fundamento seguro. Minhas primeiras experincias ensinando nas casas e pontos de pregao j apontavam para essa convico que s se fortaleceu ao longo dos anos.</li>
          <li><b>Viso missionria e plantao de igreja:</b> O ensino fiel da Palavra no pode estar desassociado do imperativo missionrio de Cristo. A compreenso da grandeza de Deus e da Sua graa salvadora deve nos impulsionar a levar o Evangelho a todos os povos. Por isso, cultivo uma viso missionria que anseia pela expanso do Reino de Deus. A plantao de novas igrejas  uma estratgia vital nesse processo, criando novas comunidades onde a Palavra  pregada, os sacramentos administrados e os crentes so discipulados. A formao especfica no CTM-SPN veio reforar esse chamado e me equipar melhor para esse desafio. Utilizar todos os dons e ferramentas disponveis, inclusive os conhecimentos adquiridos na rea de Cincia da Computao, para alcanar mais pessoas e apoiar o estabelecimento de novas frentes de trabalho,  parte integrante dessa viso.</li>
        <p>Ensinar a Palavra, fundamentado nestes princpios,  para mim mais do que um dever ministerial;  a expresso da minha gratido a Deus por Sua maravilhosa graa em minha vida, um privilgio que busco exercer com temor, zelo e amor pela verdade que liberta e transforma. Que o Senhor me conceda graa para continuar fiel a este chamado at o fim.</p>
      </section>

      {/* Opcional: Seo para fotos adicionais (ministrio, famlia, etc.) */}
      {/*
      <section className="about-gallery">
        <h2>Galeria</h2>
        <div className="gallery-images">
          <img src="/images/foto-ministerio-1.jpg" alt="Foto do Ministrio 1" />
          <img src="/images/foto-familia-1.jpg" alt="Foto da Famlia" />
          </div>
      </section>
      */}

    </div>
  );
};

export default About;